import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {omit} from 'ramda';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class SubmitButton extends Component {
	
	constructor(props) {
       super(props);
       this.state = {
          value: props.value
	   }
	   this.setPropsOverride = this.setPropsOverride.bind(this);
	}

    UNSAFE_componentWillReceiveProps(newProps) {
       if (newProps.value !== this.state.value) this.setState({ value: newProps.value });
    }

    setPropsOverride() {
	  console.log(this.props.n_clicks)
	  n_clicks_timestamp: Date.now()
	  let result = ( this.props.n_clicks % 2  == 0) ? true : false ;
      // display the result
      console.log(`The number is ${result}.`);
      this.setState({ value : result });
      if (this.props.setProps) this.props.setProps({ value: result , n_clicks: this.props.n_clicks + 1,  n_clicks_timestamp: Date.now()});
    }
		
    render() {
        const {id, label, setProps, value} = this.props;
        const dataAttributes = {};
        if(this.props.loading_state && this.props.loading_state.is_loading) {
                 dataAttributes['data-dash-is-loading'] = true;
        }
  		
        return (
            <button
            onClick={this.setPropsOverride}
            {...omit(['value', 'loading_state', 'setProps'], this.props)}
            {...dataAttributes}
        >
            {this.props.children}
        </button>
        );
    }
}

SubmitButton.defaultProps = {
	value: false,
	n_clicks: 0,
	n_clicks_timestamp: -1,
};

SubmitButton.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,
	
	/**
     * An integer that represents the number of times
     * that this element has been clicked on.
     */
    'n_clicks': PropTypes.number,
	
	/**
     * An integer that represents the time (in ms since 1970)
     * at which n_clicks changed. This can be used to tell
     * which button was changed most recently.
     */
    'n_clicks_timestamp': PropTypes.number,

    /**
     * A label that will be printed when this component is rendered.
     */
    label: PropTypes.string.isRequired,

    /**
     * The value is True or False.
     */
    value: PropTypes.bool,
	/**
	
	/**
    * Often used with CSS to style elements with common properties.
    */
    'className': PropTypes.string,
	
	/**
     * The children of this component
     */
    'children': PropTypes.node,
	
	/**
     * Defines CSS styles which will override styles previously set.
     */
    'style': PropTypes.object,
    
	/**
     * Text to be displayed in a tooltip when hovering over the element.
     */
    'title': PropTypes.string,

    /**
     * Object that holds the loading state object coming from dash-renderer
     */
    'loading_state': PropTypes.shape({
        /**
         * Determines if the component is loading or not
         */
        is_loading: PropTypes.bool,
        /**
         * Holds which property is loading
         */
        prop_name: PropTypes.string,
        /**
         * Holds the name of the component that is loading
         */
        component_name: PropTypes.string,
    }),

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};
